import Vue from 'vue'
import Router from 'vue-router'
// import index from '@/components/index.vue'
const index = (resolve) => {
  import('../components/index.vue').then((module) => {
    resolve(module)
  })
}
// import shopping from '@/components/shopping/shopping.vue'
const shopping = (resolve) => {
  import('../components/shopping/shopping.vue').then((module) => {
    resolve(module)
  })
}
// import goodlist from '@/components/goodslist/goodlist.vue'
const goodlist = (resolve) => {
  import('../components/goodslist/goodlist.vue').then((module) => {
    resolve(module)
  })
}
// import person from '@/components/person/person.vue'
const person = (resolve) => {
  import('../components/person/person.vue').then((module) => {
    resolve(module)
  })
}
// import Orderbanner from '@/components/order/order_banner/order_banner.vue'
const Orderbanner = (resolve) => {
  import('../components/order/order_banner/order_banner.vue').then((module) => {
    resolve(module)
  })
}
// import allget from '@/components/order/get.vue'
const allget = (resolve) => {
  import('../components/order/get.vue').then((module) => {
    resolve(module)
  })
}
// import allend from '@/components/order/end.vue'
const allend = (resolve) => {
  import('../components/order/end.vue').then((module) => {
    resolve(module)
  })
}
// import allall from '@/components/order/order_all/order_all.vue'
const allall = (resolve) => {
  import('../components/order/order_all/order_all.vue').then((module) => {
    resolve(module)
  })
}
// import allmsg from '@/components/order/msg.vue'
const allmsg = (resolve) => {
  import('../components/order/msg.vue').then((module) => {
    resolve(module)
  })
}
// import allpay from '@/components/order/pay.vue'
const allpay = (resolve) => {
  import('../components/order/pay.vue').then((module) => {
    resolve(module)
  })
}
// import get from '@/components/order/order_get/order_get.vue'
const get = (resolve) => {
  import('../components/order/order_get/order_get.vue').then((module) => {
    resolve(module)
  })
}
// import getdetail from '@/components/order/order_get/order_getdetail/ordergetdetail.vue'
const getdetail = (resolve) => {
  import('../components/order/order_get/order_getdetail/ordergetdetail.vue').then((module) => {
    resolve(module)
  })
}
// import payy from '@/components/order/order_pay/order_pay.vue'
const payy = (resolve) => {
  import('@/components/order/order_pay/order_pay.vue').then((module) => {
    resolve(module)
  })
}
// import paydetail from '@/components/order/order_pay/order_paydetail/orderpaydetail.vue'
const paydetail = (resolve) => {
  import('../components/order/order_pay/order_paydetail/orderpaydetail.vue').then((module) => {
    resolve(module)
  })
}
// import msg from '@/components/order/order_msg/order_msg.vue'
const msg = (resolve) => {
  import('../components/order/order_msg/order_msg.vue').then((module) => {
    resolve(module)
  })
}
// import msgdetail from '@/components/order/order_msg/order_msgdetail/ordermsgdetail.vue'
const msgdetail = (resolve) => {
  import('../components/order/order_msg/order_msgdetail/ordermsgdetail.vue').then((module) => {
    resolve(module)
  })
}
// import pay from '@/components/pay/pay.vue'
const pay = (resolve) => {
  import('../components/pay/pay.vue').then((module) => {
    resolve(module)
  })
}
// import Chooseadress from '@/components/choose_address/Choose_Address'
const Chooseadress = (resolve) => {
  import('../components/choose_address/Choose_Address.vue').then((module) => {
    resolve(module)
  })
}
// import Addaddress from '@/components/add_address/Add_Address'
const Addaddress = (resolve) => {
  import('../components/add_address/Add_Address.vue').then((module) => {
    resolve(module)
  })
}
// import detailstotal from '@/components/details/details'
const detailstotal = (resolve) => {
  import('../components/details/details.vue').then((module) => {
    resolve(module)
  })
}
// import goodsnorm from '@/components/details_goods_norms/details_goods_norms'
const goodsnorm = (resolve) => {
  import('@/components/details_goods_norms/details_goods_norms.vue').then((module) => {
    resolve(module)
  })
}
// import paypic from '@/components/paypic/paypic'
const paypic = (resolve) => {
  import('../components/paypic/paypic.vue').then((module) => {
    resolve(module)
  })
}
// import login from '@/components/login/login.vue'
const login = (resolve) => {
  import('../components/login/login.vue').then((module) => {
    resolve(module)
  })
}
// import register from '@/components/register/register.vue'
const register = (resolve) => {
  import('../components/register/register.vue').then((module) => {
    resolve(module)
  })
}
// import feet from '@/components/feet/feet.vue'
const feet = (resolve) => {
  import('../components/feet/feet.vue').then((module) => {
    resolve(module)
  })
}
// import manage from '@/components/manage/manage.vue'
const manage = (resolve) => {
  import('../components/manage/manage.vue').then((module) => {
    resolve(module)
  })
}
// import usercollect from '@/components/usercollect/usercollect.vue'
const usercollect = (resolve) => {
  import('../components/usercollect/usercollect.vue').then((module) => {
    resolve(module)
  })
}
// import changenum from '@/components/changenum/change-num.vue'
const changenum = (resolve) => {
  import('../components/changenum/change-num.vue').then((module) => {
    resolve(module)
  })
}
// import changepw from '@/components/changepw/change-pw.vue'
const changepw = (resolve) => {
  import('../components/changepw/change-pw.vue').then((module) => {
    resolve(module)
  })
}
Vue.use(Router)
export default new Router({
  // mode: 'history',
  // base: '/B_MakeupTao/',
  routes: [
    {
      title: '首页',
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/',
      redirect: '/index'
    },
    {
      title: '支付',
      path: '/pay',
      name: 'pay',
      component: pay
    },
    {
      tite: '购物车',
      path: '/shopping',
      name: 'shopping',
      component: shopping
    },
    {
      title: '商品目录',
      path: '/goodlist',
      name: 'goodlist',
      component: goodlist
    },
    {
      title: '我的订单',
      path: '/order',
      name: 'order',
      component: Orderbanner
    },
    {
      title: '待付款订单详情',
      path: '/getdetail',
      name: 'getdetail',
      component: getdetail
    },
    {
      title: '待收货订单详情',
      path: '/paydetail',
      name: 'paydetail',
      component: paydetail
    },
    {
      title: '待评价订单详情',
      path: '/msgdetail',
      name: 'msgdetail',
      component: msgdetail
    },
    {
      title: '收货',
      path: '/allget',
      name: 'allget',
      component: allget,
      children: [
        {
          title: '待收货',
          path: '/order/nav/get',
          name: 'get',
          component: get
        }
      ]
    },
    {
      title: '完成',
      path: '/allend',
      name: 'allend',
      component: allend
    },
    {
      title: '付款',
      path: '/allpay',
      name: 'allpay',
      component: allpay,
      children: [
        {
          title: '待付款',
          path: '/order/nav/pay',
          name: 'payy',
          component: payy
        }
      ]
    },
    {
      title: '评价',
      path: '/allmsg',
      name: 'allmsg',
      component: allmsg,
      children: [
        {
          title: '待评价',
          path: '/order/nav/msg',
          name: 'msg',
          component: msg
        }
      ]
    },
    {
      title: '全部的',
      path: '/allall',
      name: 'allall',
      component: allall
    },
    {path: '/Choose_Address', component: Chooseadress},
    {path: '/Add_Address', component: Addaddress},
    {path: '/details', component: detailstotal},
    {path: '/details_goods_norms', component: goodsnorm},
    {path: '/paypic', component: paypic},
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/register',
      name: 'register',
      component: register
    },
    {path: '/person', name: 'person', component: person},
    {path: '/feet', name: 'feet', component: feet},
    {path: '/manage', name: 'manage', component: manage},
    {path: '/usercollect', name: 'usercollect', component: usercollect},
    {path: '/changenum', name: 'changenum', component: changenum},
    {path: '/changepw', name: 'changepw', component: changepw}
  ]
})



